    const svg = rough.svg(this.$refs.svg);
    let node = svg.rectangle(10, 10, 200, 200, {fill: "red"}); // x, y, width, height
    this.$refs.svg.appendChild(node);
    let svg_string = json;
    console.log(svg_string);
    for(let grp of svg_string.children) {
        if (grp.attributes) {

            if (grp.attributes.hasOwnProperty('id')) {

                if (grp.attributes.id == "constLines"){

                    for (let cg of grp.children) {
                    
                        // console.log("2", svg.line(cg.attributes.d, {roughness: 0.5}))
                        if (!cg.attributes.d) {
                          continue;
                        }
                        let realD = cg.attributes.d.replace(/,/g, ", ");
                        console.log("1", realD)
                        let line = svg.line(realD, {roughness: 0.5});
                        parse(line).then(linejs => {
                          for(let grp of linejs.children) {
                            let d = grp.children[0].attributes.d;
                            console.log("d", d);
                            cg.attributes.d = d;
                          }
                          console.log("------------------------------------------")
                        })
                        
                        
                    }
                } 
